"use strict";

let GameHttp = require("./GameHttp");
let Md5 = require("./../encrypt/Md5").md5_hex_hmac;
let UtilsCross = require("./../platform/PlatformUtils");

let UCRETRY = 5;  // ucai5
let LOGINRETRY = 5;  // 

// let urlroot = "http://192.168.132.71:8080/zc_game?m="; // 
// let urlroot = "http://106.75.93.189:8080/zc_game?m="; // 

let port = [8010,8011,8012,8015,8016,8017][Math.round(Math.random() * 5)];
let urlroot = "http://mini-game.zhanyougame.com:" + port + "/zc_game?m="; //  8010,8011,8012,8015,8016,8017

let encryptKey = "zygame";

let HttpProxy = cc.Class({
    statics: {
        instance: null,
        getInstance() {
            if (!this.instance) {
                this.instance = new HttpProxy();
            }
            return this.instance;
        }
    },

    /**
     * 
     * @param onSuc
     * @param onFailed
     */
    login(onSuc, onFailed){
        cc.log("===urlroot:" + urlroot);

        let data = {
            energy: 1,  // 
            // focusingatt: GameManager.getCurrSkillTime(),  // 
            // vip: GameManager.getPlayerLevel(),  // vip
            otherpassplies: 1,  // 
            // kaleidoscopeatt: GameManager.getCurrSkillTime(),  // 
            // salvoatt: GameManager.getCurrSkillTime(),  // 
            loginday: 1,  // 
            diamond: 10,  // 
            // gold: GameManager.getPlayerGold(),  // 
            allautoatt: 10, // 
            normalpassplies: 1,  // 
            cversion: UtilsCross.getAppVersion(),  // 
            healthlevel: zy.dataMng.userData.hpLevel,
            goldrewardlevel: zy.dataMng.userData.freeCoinsLevel,
            // level: GameManager.getLevel(),
            // experience: GameManager.getPlayer_UpgradeprogressNum(), // 
            stamina: zy.dataMng.userData.phPower, // 
            channel: CHANNEL_ID,
            macaddress: zy.device.mac,
            idfa: zy.device.idfa,


        };
        let url = urlroot + "user_join_game";
        let failCb = ()=>{
            if (onFailed) {
                onFailed();
            }
            if (LOGINRETRY > 0) {
                LOGINRETRY -= 1;
                setTimeout(()=>{
                    this.login();
                }, 5000);
            }
        };

        this.serverRequest(url, data, onSuc, failCb);
    },

    /**
     * 
     * @param id{Number} 0,1,2vip,3,4,5,6
     * @param value
     * @param onSuc
     * @param onFailed
     */
    updateBase(id, value, onSuc, onFailed) {
        let data = {
            baseinfoid: id,
            value: value,
        };

        let url = urlroot + "base_info_change";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    /**
     * 
     * @param onSuc
     * @param onFailed
     *
     * id 0:,1:,2:,3:,4:,5:
     * lock 0:1:
     */
    updateTurret(id, level, star, lock, onSuc, onFailed){
        let data = {
            level: level,// 
            turretid: id, // id
            star: star, // 
            lock: lock, // 1
        };

        let url = urlroot + "turret_info";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    /**
     * 
     * @param onSuc
     * @param onFailed
     *
     * id 0:,1:
     * 0:1:
     **/
    updateBuilding(id, lock, onSuc, onFailed) {
        let data = {
            buildingid: id,
            lock: lock,
        };
        let url = urlroot + "building_info";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    /**
     * 
     * @param onSuc
     * @param onFailed
     */
    updateTreasure(id, lock, onSuc, onFailed) {
        let data = {
            treasureid: id,
            lock: lock,
        };
        let url = urlroot + "treasure_info";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    /**
     * 
     * @param placeId {string} 
     * @param onSuc
     * @param onFailed
     */
    watchAds(placeId, onSuc, onFailed) {
        let data = {
            adstationid: placeId,
        };
        let url = urlroot + "watch_advertisement";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    /**
     * 
     * @param btnId {string} 
     * @param onSuc
     * @param onFailed
     */
    clickButton(btnId, onSuc, onFailed) {
        let data = {
            buttonid: btnId,
        };
        let url = urlroot + "click_button";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    getServerTime(onSuc, onFailed) {
        let data = {
        };
        let url = urlroot + "request_unixtime";
        this.serverRequest(url, data, onSuc, onFailed);
    },

    /**
     *
     * @param url
     * @param data
     * @param onSuc
     * @param onFailed
     */
    serverRequest(url, data, onSuc, onFailed){
        cc.log("===>serverRequest: " + typeof data + " | " + JSON.stringify(data));
        data = typeof data == "string" ? data : JSON.stringify(data);
        // 
        let encryptStr = Md5(encryptKey, data);
        let uid = UtilsCross.getMobilePhoneID();
        uid = uid == undefined ? "" : uid;
        cc.log("uid=" + uid);
        let newData = {
            data: JSON.parse(data),
            encrypt: encryptStr,
            roleid: uid,
            token: ""
        };

        newData = JSON.stringify(newData);

        GameHttp.httpPost(url, newData, (rep)=>{
            cc.log("===>response:" + rep.getBody());
            if (rep.isOk()){
                cc.log("===>requrest: " + url + " ");
                if (onSuc){
                    onSuc(JSON.parse(rep.getBody()));
                }
            } else {
                cc.log("===>requrest: " + url + " ");
                if (onFailed){
                    onFailed(rep.getError() || rep.getBody());
                }
            }
        });
    }
});
